.gobang{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;width:80vw;margin:0 auto}.board{position:relative;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1;min-width:500px;min-height:500px;margin:0 auto}.cells{position:absolute}.info{position:relative;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;height:100vh}.info__button{display:block;-webkit-transition:all .2s ease-in;-o-transition:.2s all ease-in;transition:all .2s ease-in;width:200px;height:100px;margin:0 auto;padding:20px;border:2px solid #333;-webkit-border-radius:5px;border-radius:5px;background-color:#fff;color:#333}.info__button--wins{border-color:#f03e3e;background-color:#f03e3e;color:#fff}.info__button:hover{border-color:#333;background-color:#333;color:#fff}@media (max-width:687px){.gobang{-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:start;-webkit-justify-content:flex-start;-ms-flex-pack:start;justify-content:flex-start}.info{height:auto}}